  font-size: 3.5rem; }

h3 {
  font-size: 3rem; }

/*UTILIDADES*/
.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

li {
  list-style: none; }

img {
  max-width: 100%; }

/*EFECTO SCROLL*/
.bgVerde {
  background-color: #8fd9a8; }

.bgAmarillo {
  background-color: #d2e69c; }

.header {
  background: #28b5b5; }
  .header .contenidoHeader {
    padding: 1.66667rem 0; }
    @media screen and (min-width: 768px) {
      .header .contenidoHeader {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    text-align: center;
    color: #fff;
    margin-bottom: 1rem; }
    @media screen and (min-width: 768px) {
      .header h1 {
        margin-top: 0; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 0.6rem 0.7rem rgba(0, 0, 0, 0.6); }

.navMenu {
  display: flex;
  gap: 2rem;
  justify-content: center; }
  .navMenu a {
    color: #fff;
    font-size: 2rem;
    font-weight: 600; }
    .navMenu a:hover {
      color: #d2e69c; }

.video {
  position: relative;
  max-height: 40rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+71,ffffff+100&0.8+0,0.5+100 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.59) 71%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.59) 71%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.59) 71%, rgba(255, 255, 255, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    width: 100%; }

.contenidoVideo {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 2; }
  .contenidoVideo h2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 5rem;
    color: #fff; }
    .contenidoVideo h2 span {
      padding: 1rem 2rem;
      font-size: 2rem;
      background-color: #28b5b5; }

.festival {
  padding: 5rem 0; }
  @media screen and (min-width: 768px) {
    .festival {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 5rem; } }

.contenidoFestival {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .contenidoFestival h2 {
    font-size: 3.5rem;
    margin-bottom: 2rem; }
  .contenidoFestival .fecha {
    margin-bottom: 2rem;
    font-size: 2rem;
    font-weight: 600;
    color: #4b778d; }
  .contenidoFestival .texto {
    line-height: 1.5; }

.lineUp {
  background-color: #4b778d;
  padding: 5rem 0; }
  .lineUp h3, .lineUp p {
    text-align: center;
    color: #fff;
    font-weight: 600; }
  .lineUp .dia {
    font-size: 2rem;
    text-transform: uppercase;
    margin: 5rem 0; }

@media screen and (min-width: 768px) {
  .contenedorEscenario {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5rem; } }

.contenedorEscenario .escenario {
  padding-top: 2.5rem; }

.contenedorEscenario .nombreEscenario {
  color: #000; }
  .contenedorEscenario .nombreEscenario:after {
    content: "";
    width: 20rem;
    height: 10rem;
    display: block;
    margin: 2rem auto 0 auto;
    background-size: 100px;
    background-repeat: no-repeat;
    background-position: center center; }

.edm .nombreEscenario:after {
  background-image: url("/build/img/icono_edm.png"); }

.rock .nombreEscenario:after {
  background-image: url("/build/img/icono_rock.png"); }

.calendario li {
  padding: 2.5rem;
  font-weight: 800;
  text-transform: uppercase; }
  .calendario li span {
    font-weight: 600; }

.bgVerde .calendario li:nth-child(odd) {
  background-color: #7cd399; }

.bgAmarillo .calendario li:nth-child(odd) {
  background-color: #c9e188; }

.galeria {
  padding: 5rem 0; }
  .galeria h3 {
    text-align: center;
    margin-bottom: 5rem;
    text-transform: uppercase;
    color: #4b778d;
    font-size: 4rem;
    font-weight: 600; }

.galeriaImagenes {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem; }
  @media screen and (min-width: 768px) {
    .galeriaImagenes {
      grid-template-columns: repeat(4, 1fr);
      gap: 1.66667rem; } }
  @media screen and (min-width: 1200px) {
    .galeriaImagenes {
      grid-template-columns: repeat(6, 1fr);
      gap: 2.5rem; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }
  .overlay img {
    max-width: 90rem; }
  .overlay .botonCerrar {
    color: #fff;
    font-size: 3rem;
    font-weight: 600;
    display: grid;
    place-items: center;
    width: 4rem;
    height: 4rem;
    background-color: #4b778d;
    border-radius: 50%; }
    .overlay .botonCerrar:hover {
      cursor: pointer; }

.boletos {
  padding: 5rem 0;
  background: #4b778d; }
  .boletos h3 {
    text-align: center;
    font-size: 3rem;
    margin-bottom: 3rem;
    font-weight: 600;
    color: #fff; }
  .boletos .plan p {
    color: #fff;
    margin-bottom: 2rem;
    text-align: center;
    font-size: 2.5rem; }
  .boletos .contenedorBoletos {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2.5rem; }

.pase {
  width: 100%;
  background-color: #fff;
  border-spacing: 0; }
  .pase thead {
    background-color: #28b5b5; }
    .pase thead th {
      font-weight: 800;
      padding: 1.4rem;
      color: #fff;
      text-transform: uppercase; }
  .pase tbody {
    background-color: #fff; }
    .pase tbody td {
      padding: 1rem;
      border-bottom: 1px solid #000; }
    .pase tbody tr:last-of-type td {
      border: none; }
  .pase tfoot {
    background-color: #8fd9a8; }
    .pase tfoot td {
      color: #000;
      font-weight: 800;
      padding: 1rem;
      text-align: center;
      font-size: 2rem; }

footer {
  width: 100%;
  background-color: #28b5b5;
  padding: 0; }
  footer p {
    font-weight: 600;
    padding: 2.5rem;
    text-align: center;
    font: 2rem;
    color: #fff; }
